import React, { useState, useEffect } from 'react';
import CloseBtn from '../button/CloseBtn';
import GoldboxContainer from '../../containers/GoldboxContainer';
import GetCharaContainer from '../../containers/GetCharaContainer';
import SetCharaContainer from '../../containers/SetCharaContainer';
import CharaListContainer from '../../containers/CharaListContainer';

const Board = () => {
  const [ apply, setApply ] = useState(false);
  const [ more, setMore ] = useState(false);
  
  //    toggle
  const toggleApply = () => {
    setApply(!apply);
  }

  //     toggle
  const toggleMore = () => {
    setMore(!more);
    setApply(false);
  }

  return (
    <div className="board">
      {/*  */}
      {!apply && !more &&
        <>
          <GoldboxContainer />
          <CharaListContainer
            toggleApply={toggleApply}
          />
        </>
      }

      {/*    */}
      {apply &&
        <>
          <CloseBtn 
            onClick={toggleApply}
          />
          <GetCharaContainer 
            toggleMore={toggleMore}
          />
        </>
      }

      {/*     */}
      {more &&
        <>
          <CloseBtn 
            onClick={toggleMore}
          />
          <SetCharaContainer 
            toggleMore={toggleMore}
          />
        </>
      }
    </div>
  );
};

export default Board;