import React, { useState, useEffect } from 'react';
import RaidGoldForm from '../form/RaidGoldForm';

const raidsData = [
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1340, gold: 500},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1370, gold: 600},
  {name: '', dataName: '', difficulty: 'normal', minLevel: 1370, gold: 1000},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1415, gold: 1200},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1445, gold: 1800},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1430, gold: 1600},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1460, gold: 2400},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1475, gold: 3000},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1490, gold: 7000},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1540, gold: 9000},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1540, gold: 4500},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1580, gold: 6500},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1580, gold: 7500},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1600, gold: 10000},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1600, gold: 9000},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1620, gold: 14500},
  {name: ' ', dataName: '', difficulty: 'normal', minLevel: 1610, gold: 13000},
  {name: ' ', dataName: '', difficulty: 'hard', minLevel: 1630, gold: 41000},
]

const RaidList = ({ charaRaids, onSelect, onDelete, onCheck, onSubmit }) => {
  const [ toggle, setToggle ] = useState(false);
  const [ total, setTotal ] = useState(0);

  //   set
  useEffect(() => {
    const checkGold = () => {
      let totalGold = 0;

      if ( charaRaids.length > 0 ) {
        //  
        let comp = charaRaids.filter(item => item.completed);

        if ( comp.length > 0 ) {
          comp.map(item => {
            totalGold += parseInt(item.gold);
          });
        }
      }

      return totalGold;
    }

    setTotal(checkGold);
  }, [ charaRaids ]);

  return (
    <>
      <ul className="raids__list">
        {charaRaids.map((item, i) => (
          <li key={i} className={`${item.completed ? 'completed': ''}`}>
            <div className="box">
              <span className={`icon ${item.name}`}>
                {item.name}
              </span>
              <span className="level">
                {item.difficulty}
              </span>

              {/* /  */}
              <div className="update">
                <button type="button" className="btn btn__check" onClick={() => onCheck(item.name)}></button>
                <button type="button" className="btn btn__close" onClick={() => onDelete(item.name)}></button>
              </div>
            </div>
            <span className="gold">
              <RaidGoldForm onSubmit={onSubmit} gold={item.gold} index={i} />
            </span>
          </li>
        ))}
        {charaRaids.length < 3 && 
          <li className={`add ${toggle ? 'active' : ''}`} onClick={() => setToggle(!toggle)}>
            <div className="box">
              <span className="icon__add">
              </span>
              {toggle &&
                <ul className="raids__select custom_scrollbar">
                  {raidsData.map((item, i) => (
                    <li key={i} onClick={onSelect} data-name={item.dataName} data-difficulty={item.difficulty} data-gold={item.gold}>{item.name}</li>
                  ))}
                </ul>
              }
            </div>
          </li>
        }
      </ul>

      <span className="total_gold">{total}</span>
    </>
  );
};

export default RaidList;